import React, { useEffect, useState } from "react";
import styles from "./index.module.scss";
import { Input, Button } from "../../ui";
import { maybePluralize } from "../../utils";
export function Content() {
  const [value, setValue] = useState();
  const [randomNumber, setRandomNumber] = useState(0);
  const [result, setResult] = useState("");
  const [tryCount, setTryCount] = useState(0);

  function handleChange(e) {
    setValue(parseInt(e.target.value));
  }
  useEffect(() => {
    setRandomNumber(parseInt(Math.random() * 100));
  }, []);

  let maxTryCount = 5;

  function handleSubmit(e) {
    e.preventDefault();

    if (value === randomNumber) {
      setResult("–í—ã —É–≥–∞–¥–∞–ª–∏!");
    } else if (value > randomNumber) {
      setResult(
        "–í—ã –≤–≤–µ–ª–∏ —Å–ª–∏—à–∫–æ–º –±–æ–ª—å—à–æ–µ —á–∏—Å–ª–æ.–ü–æ–ø—Ä–æ–±—É–π—Ç–µ –µ—â–µ —Ä–∞–∑. –í–≤–µ–¥–∏—Ç–µ —á–∏—Å–ª–æ –æ—Ç 1 –¥–æ 100"
      );
    } else if (value < randomNumber) {
      setResult(
        "–í—ã –≤–≤–µ–ª–∏ —Å–ª–∏—à–∫–æ–º –º–∞–ª–µ–Ω—å–∫–æ–µ  —á–∏—Å–ª–æ.–ü–æ–ø—Ä–æ–±—É–π—Ç–µ –µ—â–µ —Ä–∞–∑. –í–≤–µ–¥–∏—Ç–µ —á–∏—Å–ª–æ –æ—Ç 1 –¥–æ 100"
      );
    }
    if (tryCount >= maxTryCount) {
      setResult(
        `–£ –≤–∞—Å –∑–∞–∫–æ–Ω—á–∏–ª–∏—Å—å –ø–æ–ø—ã—Ç–∫–∏, –ø—Ä–∞–≤–∏–ª—å–Ω—ã–π –æ—Ç–≤–µ—Ç: ${randomNumber}. –ù–∞—á–Ω–∏—Ç–µ –Ω–æ–≤—É—é –∏–≥—Ä—É`
      );
    }
    if (tryCount === maxTryCount) {
      setValue("");
      setTryCount(0);
      return;
    }

    setValue("");
    setTryCount((tryCount) => tryCount + 1);
  }

  return (
    <form onSubmit={handleSubmit} className={styles.form}>
      <h2 className={styles.title}>–ò–≥—Ä–∞ —É–≥–∞–¥–∞–π–∫–∞</h2>
      <div className={styles.result}>{result}</div>
      <Input
        onChange={handleChange}
        value={value}
        label="–£–≥–∞–¥–∞–π—Ç–µ —á–∏—Å–ª–æ –æ—Ç 0 –¥–æ 100"
        name="userAnswers"
        type="number"
        placeholder="–í–≤–µ–¥–∏—Ç–µ —á–∏—Å–ª–æ"
      />
      <div className={styles.try}>
        –£ –≤–∞—Å –æ—Å—Ç–∞–ª–æ—Å—å:
        {maybePluralize(maxTryCount - tryCount, "–ø–æ–ø—ã—Ç", "–∫–∏", "–∫–∞", "–æ–∫")}
      </div>
      <Button label="–û—Ç–≤–µ—Ç–∏—Ç—å" />
    </form>
  );
}
