import React, { useState, useReducer } from "react";
import styles from "./index.module.scss";
import { Input, Button } from "../../ui";
import { maybePluralize } from "../../utils";

function reducer(state, action) {
  return { ...state, [action.name]: action.payload };
}

const initialState = {
  userAnswer1: "",
  userAnswer2: "",
  userAnswer3: "",
};

let answers = {
  userAnswer1: "–≤–æ–¥–æ–ø—Ä–æ–≤–æ–¥",
  userAnswer2: "—Ç–∏—à–∏–Ω–∞",
  userAnswer3: "–Ω–æ–∂–Ω–∏—Ü—ã",
};

export function Form() {
  const [data, dispatch] = useReducer(reducer, initialState);
  const [score, setScore] = useState(0);
  const [error, setError] = useState({});

  function handleChange(e) {
    dispatch({
      name: e.target.name,
      payload: e.target.value,
    });
  }

  function handleSubmit(e) {
    e.preventDefault();
    e.target.reset();
    let score = 0;
    let errors = {};
    let arr = Object.keys(answers);
    arr.forEach((key) => {
      if (data[key] === answers[key]) {
        score++;
      } else {
        errors[key] = "–í—ã –æ—Ç–≤–µ—Ç–∏–ª–∏ –Ω–µ–ø—Ä–∞–≤–∏–ª—å–Ω–æ!";
      }
    });

    setScore(score);
    setError(errors);
  }

  return (
    <form onSubmit={handleSubmit} className={styles.form}>
      <h2 className={styles.title}>–ò–≥—Ä–∞ –≤ –∑–∞–≥–∞–¥–∫–∏</h2>

      <Input
        onChange={handleChange}
        errors={error}
        name="userAnswer1"
        placeholder="–í–≤–µ–¥–∏—Ç–µ –æ—Ç–≥–∞–¥–∫—É..."
        label="–†–µ—á–∫–∞ —Å–ø—è—Ç–∏–ª–∞ —Å —É–º–∞ ‚Äî –ü–æ –¥–æ–º–∞–º –ø–æ—à–ª–∞ —Å–∞–º–∞."
      />
      <Input
        errors={error}
        onChange={handleChange}
        name="userAnswer2"
        placeholder="–í–≤–µ–¥–∏—Ç–µ –æ—Ç–≥–∞–¥–∫—É..."
        label="–ù–µ –≤–∏–¥–Ω–æ –µ–µ, –∏ –Ω–µ —Å–ª—ã—à–Ω–æ –µ–µ. –ù–æ —Å—Ç–æ–∏—Ç –∑–∞–≥–æ–≤–æ—Ä–∏—Ç—å –æ –Ω–µ–π, –∫–∞–∫ –æ–Ω–∞ –∏—Å—á–µ–∑–∞–µ—Ç."
      />
      <Input
        errors={error}
        onChange={handleChange}
        name="userAnswer3"
        placeholder="–í–≤–µ–¥–∏—Ç–µ –æ—Ç–≥–∞–¥–∫—É..."
        label="–î–≤–∞ –∫–æ–Ω—Ü–∞, –¥–≤–∞ –∫–æ–ª—å—Ü–∞, –ø–æ—Å–µ—Ä–µ–¥–∏–Ω–µ –≥–≤–æ–∑–¥–∏–∫."
      />
      <div className={styles.score}>
        {" "}
        –í—ã –æ—Ç–≥–∞–¥–∞–ª–∏: {maybePluralize(score, "–∑–∞–≥–∞–¥", "–∫–∏", "–∫—É", "–æ–∫")}
      </div>
      <Button label="–û—Ç–≤–µ—Ç–∏—Ç—å" type="submit" />
    </form>
  );
}
